.card-container {
    display : inline-block;
    cursor: pointer;
    overflow: hidden;
    width: calc(30% - 1%);
    height : 400px;
    margin-left : 1%;
    margin-right :1%;
    margin-bottom : 20px;
    background: #fff;
    border-radius: 5px;
    box-shadow: 0 5px 20px 0 rgba(0, 0, 0, 0.1);
    border: 1px solid rgba(0, 168, 255, 0.3);
    text-decoration: none;

}

